@use "../import"as f;

body {
	font-family: 'Noto Sans JP',
		"Helvetica Neue",
		"Segoe UI",
		"Hiragino Kaku Gothic ProN",
		"Hiragino Sans",
		Meiryo, sans-serif;
	background: #fff;
	position: relative;
	color: f.$colorBlack;
}

section {
	height: 100vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: aquamarine;

	@include f.mq(md) {
		background-color: brown;
	}
}

button {
	font-family: 'Noto Sans JP', sans-serif;
}

