.overlay-container {
	position: fixed;
	z-index: 110;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.overlay {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	// z-index: 99;
	// overflow: hidden;

	&.first {
		background: #fcc419;
	}

	&.second {
		background: #fcc419;
		left: 33.33%;
	}

	&.third {
		background: #fcc419;
		left: 66.66%;
	}
}