@charset "UTF-8";
body {
  font-family: "Noto Sans JP", "Helvetica Neue", "Segoe UI", "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  background: #fff;
  position: relative;
  color: #202020;
}

section {
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: aquamarine;
}
@media screen and (min-width: 768px) {
  section {
    background-color: brown;
  }
}

button {
  font-family: "Noto Sans JP", sans-serif;
}

/* Page Loader */
.overlay-container {
  position: fixed;
  z-index: 110;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}
.overlay.first {
  background: #fcc419;
}
.overlay.second {
  background: #fcc419;
  left: 33.33%;
}
.overlay.third {
  background: #fcc419;
  left: 66.66%;
}

.header {
  min-height: 100vh;
  /*for IE11*/
  min-height: calc(var(--vh, 1vh) * 100);
  background-image: url(https://drive.google.com/uc?export=view&id=1rZVg3bV-CFxvavnLTPvJ5HlIe0S_mbVn);
  /*↑ここまでが100vhの設定*/
  display: grid;
  place-items: center;
}

.header-title {
  background-color: #fff;
  display: inline-block;
  font-size: 3vw;
  padding: 1vw;
}

